<template>
    <div class="container posts pt-3">
        <div v-for="(item, index) in data" :key="index">
            <article class="singlepost container mb-1">
                <div class="row close-section">
                    <input type="text" disabled value="mohammad" v-bind:name="index" v-bind:id="index"  class="form-control w-100">
                    <a v-bind:href="item.link" target="_blank"><h5 class="mr-auto pl-2 py-1">{{item.header}}</h5></a>
                    <div class="ml-auto close-section mr-1" v-on:click="item.status = !item.status">
                        <span class="expand align-middle" v-if="item.status">-</span>
                        <span class="expand align-middle" v-else>+</span>
                    </div>
                </div>
                <div class="row  pl-2 py-1" v-if="item.status">
                    <div class="row ml-2"><p>{{item.content}}  </p></div>
                    <div class="ml-auto close-section mr-1" v-on:click="editmethod(index)"><span class="expand-p align-middle"> {{textedit(index)}}</span></div>
                    <div class=" close-section mr-1" v-on:click="deletepost(index)"><span class="expand-p align-middle">remove</span></div> 
                </div>
            </article>
        <Editme v-if="data[index].edit" v-bind:edit="edit"></Editme>
        </div>
    </div>
</template>


<script>
import Editme from './edit.vue'

export default {
    components: {
        Editme,
    },
    data() {
        return {
            data:[],
            edit:{show: false,newPost: false,alert:'',status:'',header:'',link:'',content:'',edit:false,key:''},
        }
    },
    methods:{
        editmethod:function(index){
            this.edit.header = this.data[index].header;
            this.edit.link = this.data[index].link;
            this.edit.content = this.data[index].content;
            this.edit.edit = this.data[index].edit;
            this.edit.status = this.data[index].status;
            this.edit.key = index;
            return (this.data[index].edit = !this.data[index].edit);           
        },
        textedit: function(index){
            if(this.data[index].edit){
                return 'close'
            }else{
                return 'edit';
            }
        },
        deletepost: function(index){
            firebase.database().ref('posts/'+index).remove();
        }
        
    },
    created() {
        firebase.database().ref('posts').on('value',(snapshot) => {
            this.data=snapshot.val();
        })
    }
}
</script>


<style scoped>
.singlepost{
    font-size:13px;
    border:1px solid gray;
    background-color: beige;
}
.expand{
    padding:1px 12px;
    color:orange;
    font-size:1.2rem;
    /*background-color:rgb(38, 134, 189);*/
    border-radius: 20px;

}
.close-section{
    display: -webkit-flexbox;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    text-align: center;
    height: 100%;
}
.expand:hover{
    background-color:bisque;
    color:red;
    cursor: pointer;
}
.my-btn{
    text-decoration:none;
    font-size:1.1rem;
}
.expand-p{
    padding:1px 11px;
    font-size:1.2rem;
    border-radius: 20px;
    background-color:bisque;
    color: sienna;

}
.expand-p:hover{
    color:red;
    cursor: pointer;
    padding:1px 11px;
    color:navy;
    font-size:1.2rem;
    background-color:cadetblue;
    border-radius: 20px;
}
</style>