<template>
  <div class=" mb-2">
    <form action="" method="post">
      <div class="form-group">
        <input type="search" v-model="edit.header"  v-on:keyup.enter.prevent="updatepost(edit.key)"
          class="form-control" name="" id="" aria-describedby="helpId" >
        <small id="helpId" class="form-text text-muted">Help text</small>
      </div>
      <div class="form-group">
        <input type="search" v-model="edit.link"  v-on:keyup.enter.prevent="updatepost(edit.key)"
          class="form-control" name="" id="" aria-describedby="helpId" >
        <small id="helpId" class="form-text text-muted">Help text</small>
      </div>

      <div class="form-group">
        <textarea class="form-control" name="" id="" rows="3" v-model="edit.content"  v-on:keyup.enter.prevent="updatepost(edit.key)"></textarea>
        <small id="helpId" class="form-text text-muted">Help text</small>
      </div>
      
    </form>
  </div>
</template>

<script>
export default {

props:{
    edit:{
        type:Object,
        required: true
    }
  },
  data() {
      return {

      }
  },
  methods:{
   
    updatepost:function(key){
        firebase.database().ref('posts/'+key).set({
            header:this.edit.header,
            link: this.edit.link,
            content: this.edit.content,
            status: false,
            edit: false,
        })
    }
  }
}
</script>

<style scoped>
  input,textarea{
    border: 1px solid darkblue;
    box-shadow: 1px 1px 1px;
  }
  input,textarea{
    background-color: unset;
    color: seashell;
  }
</style>