<template>
    <div class="container">
        <nav class="navigation">
            <ul>
                <li>
                    <a href="#" >Home</a>
                </li>
                <li>
                    <a href="#" v-on:click="run.newPost = !run.newPost; showSearch =false;">NewPost</a>
                </li>
                <li>
                    <a href="#" v-on:click="showSearch = !showSearch; run.newPost= false;">Search</a>
                </li>
            </ul>


        </nav>
        <transition name="fade">
            <div class="alert  alert-dismissible fade show" v-bind:class="run.status" v-if="run.show" role="alert">
                {{run.alert}}
                <button type="button" class="close" data-dismiss="alert" v-on:click="closealert" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <Newpost v-if="run.newPost" v-bind:run ="run"></Newpost>
            <Search v-if="showSearch"></Search>
        </transition>
    </div>


</template>

<script>
import Newpost from './Newpost.vue'
import Search from './Search.vue';

export default {
    components: {
        Newpost,
        Search
    },
    data () {
        return {
            showSearch: false,
            run:{show: false,newPost: false,alert:'',status:''},
        }
    },
    methods:{
        closealert:function(){
            this.run.show = false;
        }
    }
}
</script>


<style scoped>

.navigation ul{
    margin: 0px;
    list-style: none;
    background-color:indigo;
}
.navigation ul li:hover{
    background-color:deepskyblue;
}
.navigation ul li{
    border-radius: 11px;
    padding:1px 3px;
    list-style: none;
    display:inline-block;
    margin:3px 6px;
}

.navigation ul li a{
    text-decoration:none;
    color: deepskyblue;
}
.navigation ul li a:hover{
    color:floralwhite;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

</style>