<template>
  <div class=" mb-2">
    <form action="" method="post">
      <div class="form-group">
        <input type="search" v-model="post.header"  v-on:keyup.enter.prevent="send"
          class="form-control" name="" id="" aria-describedby="helpId" placeholder="Post Header ...">
        <small id="helpId" class="form-text text-muted">Help text</small>
      </div>
      <div class="form-group">
        <input type="search" v-model="post.link"  v-on:keyup.enter.prevent="send"
          class="form-control" name="" id="" aria-describedby="helpId" placeholder="Post Header ...">
        <small id="helpId" class="form-text text-muted">Help text</small>
      </div>

      <div class="form-group">
        <textarea class="form-control"  v-on:keyup.enter.prevent="send" v-model="post.content" name="" id="" rows="3" placeholder="Post description"></textarea>
        <small id="helpId" class="form-text text-muted">Help text</small>
      </div>
      
    </form>
  </div>
</template>

<script>
export default {
  props:{
    run:{
      type:Object,
      required: true
    }
  },
  data() {
    return {
        post:{header: 'this is just header one',link: 'post link ...',content: 'content for this post is here ',status: false,edit:false},
    }
  },
  methods:{
    send: function(){
      this.run.newPost = ! this.run.newPost;
      //save information into database
      console.log(firebase)
      firebase.database().ref('posts').push({header:this.post.header,link:this.post.link,content: this.post.content,status: false,edit:false})
      .then(
        (data) => {
          this.run.show = true;
          this.run.alert = 'your information was successfully inserted';
          this.run.status = 'alert-success'
        }
      )
      .catch(
        (error) => {
          this.run.show = false;
          this.run.alert = 'sorry, error was accured';
          this.run.status = 'alert-danger'

        }
      )
      ;
    }
  }
}
</script>

<style scoped>
  input,textarea{
    border: 1px solid darkblue;
    box-shadow: 1px 1px 1px;
  }
</style>